/*  API   */
function decodeField(val: string): string {
  try { return decodeURIComponent(val.replace(/\+/g, ' ')); } catch { return val; }
}

async function fetchProducts(): Promise<Record<string, ProductItem[]>> {
  try {
    const res = await fetch(`${KBANK_BASE}/ib20/act/PBKMAN0000003101A?from=mobileWeb`, {
      headers: { Referer: `${KBANK_BASE}/mw/product/list` },
    });
    if (!res.ok) throw new Error(`HTTP ${res.status}`);
    const data = await res.json();
    const body = data?._msg_?._body_;
    if (!body) throw new Error('no body');

    // fnclPdTpCdNm  tab 
    const result: Record<string, ProductItem[]> = {
      deposit: [], saving: [], card: [], loan: [], invest: [], biz: [],
    };

    const tabCodeMap: Record<string, string> = {
      '': 'deposit',
      '': 'saving',
      '': 'card',
      '': 'loan',
      '': 'invest',
      '': 'biz',
    };

    // tabList    
    const allItems: any[] = [];
    if (Array.isArray(body.tabList)) {
      body.tabList.forEach((group: any) => {
        if (Array.isArray(group)) allItems.push(...group);
        else allItems.push(group);
      });
    }
    if (Array.isArray(body.ceoTabList)) {
      body.ceoTabList.forEach((group: any) => {
        if (Array.isArray(group)) allItems.push(...group);
        else allItems.push(group);
      });
    }

    allItems.forEach((item: any) => {
      const typeNm = decodeField(item.fnclPdTpCdNm || '');
      const tabKey = tabCodeMap[typeNm];
      if (!tabKey) return;

      const product: ProductItem = {
        id: item.fnclPdId || '',
        name: decodeField(item.chnlPdNm || ''),
        title: decodeField(item.mainTit || ''),
        desc: decodeField(item.bsicIntrDesc || item.bsicIntrInfo || ''),
        imgUrl: item.listImgFile ? `${KBANK_BASE}${decodeField(item.listImgFile)}` : '',
        bgColor: decodeField(item.pdBgColr || '#f0f4ff'),
        menuId: decodeField(item.fnclPdMenuId || ''),
        badge: item.newBdgExpuYn === 'Y' ? 'N' : undefined,
        rate: item.rate ? decodeField(item.rate) : undefined,
        rateLabel: item.rtInfo ? decodeField(item.rtInfo).replace(/\$\$|%/g, '').trim() || '' : undefined,
      };

      if (result[tabKey]) result[tabKey].push(product);
    });

    //      
    const hasData = Object.values(result).some(arr => arr.length > 0);
    if (!hasData) throw new Error('empty');

    return result;
  } catch (err) {
    console.warn('[ProductListPage] API fallback:', err);
    return MOCK_PRODUCTS;
  }
}

/*    */
function ProductListPage() {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('deposit');
  const [products, setProducts] = useState<Record<string, ProductItem[]>>(MOCK_PRODUCTS);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchProducts().then(data => {
      setProducts(data);
      setLoading(false);
    });
  }, []);

  const goProduct = (item: ProductItem) => {
    const url = `${KBANK_BASE}/mw/ib20/mnu/${item.menuId}`;
    window.open(url, '_blank', 'noopener,noreferrer');
  };

  const currentList = products[activeTab] ?? [];

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <PageWrapper>
        <TopBar>
          <NavRow>
            <BackBtn onClick={() => navigate(-1)} aria-label="">
              <IcoBack />
            </BackBtn>
            <NavTitle></NavTitle>
            <NavSpacer />
          </NavRow>
          <TabScroll>
            {TABS.map(tab => (
              <TabItem
                key={tab.id}
                active={activeTab === tab.id}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.label}
              </TabItem>
            ))}
          </TabScroll>
        </TopBar>

        <ProductList>
          {loading ? (
            <LoadingBox> ...</LoadingBox>
          ) : (
            <AnimatePresence mode="wait">
              <motion.div
                key={activeTab}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0 }}
                transition={{ duration: 0.18 }}
              >
                {currentList.map((item, idx) => (
                  <div key={item.id}>
                    {idx > 0 && <ProductCardDivider />}
                    <ProductCard onClick={() => goProduct(item)}>
                      <ProductImgBox bg={item.bgColor}>
                        {item.imgUrl ? (
                          <ProductImg
                            src={item.imgUrl}
                            alt={item.name}
                            onError={(e) => { (e.target as HTMLImageElement).style.display = 'none'; }}
                          />
                        ) : (
                          <img src={LOGO_SVG} alt="K" style={{ width: '60%', opacity: 0.3 }} />
                        )}
                      </ProductImgBox>
                      <ProductInfo>
                        <ProductBadgeRow>
                          {item.badge && <ProductBadge>N</ProductBadge>}
                          <ProductName>{item.name}</ProductName>
                        </ProductBadgeRow>
                        {item.title && (
                          <ProductDesc style={{ whiteSpace: 'normal', WebkitLineClamp: 2 }}>
                            {item.title.replace(/\n/g, ' ')}
                          </ProductDesc>
                        )}
                        {item.rate && (
                          <ProductRate>
                            <RateLabel>{item.rateLabel || ''}</RateLabel>
                            <RateValue>{item.rate}</RateValue>
                          </ProductRate>
                        )}
                      </ProductInfo>
                      <ProductChevron><IcoChevronRight /></ProductChevron>
                    </ProductCard>
                  </div>
                ))}

                {currentList.length === 0 && (
                  <LoadingBox>  .</LoadingBox>
                )}
              </motion.div>
            </AnimatePresence>
          )}
        </ProductList>
      </PageWrapper>
    </ThemeProvider>
  );
}

export default ProductListPage;
