import styled from '@emotion/styled';
import { motion } from 'framer-motion';
import { useInView } from '../../hooks/useInView';
import { useEffect } from 'react';
import { kbankLogoUrl } from '../../data/mockData';

/*  eH - TopPadding */
const TopPadding = styled.div`
  width: 100%;
  height: 1rem;
`;

/*  eO - SectionCardContainer
    : border-radiusbox-shadow  
        */
const Container = styled.div`
  display: flex;
  width: 100%;
  height: 19.0625rem;
  position: relative;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
`;

/*  eV - ImageWrapper ( , ) */
const LogoBox = styled(motion.div)`
  width: 5rem;
  height: 5rem;
  position: relative;
  border-radius: 1rem;
  box-shadow: 12px 4px 24px 0px rgba(12, 9, 96, 0.24);
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 1rem;
  }
`;

/*  eq - TitleBox: position absolute bottom:0
    ,    */
const TitleBox = styled(motion.div)`
  width: 13.0625rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.125rem;
  position: absolute;
  bottom: 0;
  text-align: center;
`;

const Title = styled.h2`
  font-size: clamp(1.5rem, 4vw, 2rem);
  font-weight: 700;
  color: #020616;
  line-height: 1.35;
  white-space: pre-line;
  text-align: center;
`;

/*  eW - BottomPadding */
const BottomPadding = styled.div`
  width: 100%;
  height: 2rem;
`;

const SectionCard07 = ({ onView }: { onView?: () => void }) => {
  const { ref, inView } = useInView(0.3);

  useEffect(() => {
    if (inView) onView?.();
  }, [inView, onView]);

  return (
    <>
      <TopPadding />
      <Container
        ref={ref}
        onClick={() => window.open('https://www.kbanknow.com', '_blank')}
      >
        {/* : y 0  -50   */}
        <LogoBox
          initial={{ opacity: 0, y: 0 }}
          animate={inView ? { opacity: 1, y: -50 } : { opacity: 0, y: 0 }}
          transition={{ duration: 1 }}
        >
          <img src={kbankLogoUrl} alt="" />
        </LogoBox>

        {/* : position absolute, bottom 0,    */}
        <TitleBox
          initial={{ opacity: 0, y: -10 }}
          animate={inView ? { opacity: 1, y: -40 } : { opacity: 0, y: -10 }}
          transition={{ duration: 1, delay: 0.05 }}
        >
          <Title>{' \n '}</Title>
        </TitleBox>
      </Container>
      <BottomPadding />
    </>
  );
};

export default SectionCard07;
