import { useEffect, useRef, useState } from 'react';
import styled from '@emotion/styled';
import { investImages } from '../../data/mockData';
import { useInView } from '../../hooks/useInView';

const Container = styled.div`
  width: 100%;
  height: 37.3125rem;
  border-radius: 1.25rem;
  overflow: hidden;
  box-shadow: 0px 4px 8px 2px rgba(0,0,0,0.1);
  position: relative;
  background: #e5ebff;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  &:active { transform: scale(0.98); }
`;

const PhoneFrame = styled.div`
  width: 13.375rem;
  height: 22.9375rem;
  border-radius: 1.5rem 1.5rem 0 0;
  border: 2px solid #020616;
  overflow: hidden;
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Img = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const ItemBox = styled.div`
  position: absolute;
  left: 1.75rem;
  top: 3.25rem;
`;

const Title = styled.h2`
  font-size: clamp(1.5rem, 4vw, 2rem);
  font-weight: 700;
  color: #020616;
  line-height: 1.35;
  white-space: pre-line;
  margin-bottom: 0.5rem;
`;

const AccentText = styled.span`
  background: linear-gradient(90deg, #098bfe 3%, #4262ff 77%, #201e81 304%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: clamp(1.6rem, 5vw, 2rem);
  font-weight: 700;
`;

const SectionCard05 = ({ onView }: { onView?: () => void }) => {
  const { ref, inView } = useInView(0.4);
  const [imgIdx, setImgIdx] = useState(0);
  const timerRef = useRef<ReturnType<typeof setInterval> | null>(null);

  useEffect(() => {
    if (inView) {
      onView?.();
      timerRef.current = setInterval(() => {
        setImgIdx(p => (p + 1) % investImages.length);
      }, 2000);
    } else {
      if (timerRef.current) { clearInterval(timerRef.current); timerRef.current = null; }
    }
    return () => { if (timerRef.current) clearInterval(timerRef.current); };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inView]);

  return (
    <Container ref={ref} onClick={() => window.open('https://www.kbanknow.com', '_blank')}>
      <ItemBox>
        <Title>{' \n\n '}</Title>
        <AccentText> </AccentText>
      </ItemBox>
      <PhoneFrame>
        <Img src={investImages[imgIdx]} alt="" />
      </PhoneFrame>
    </Container>
  );
};

export default SectionCard05;
